"use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import { HiOutlineMenuAlt3, HiOutlineX } from "react-icons/hi";

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navHeight = scrolled ? "py-3" : "py-5";
  const logoScale = scrolled ? "scale-95" : "scale-100";
  const shadowClass = scrolled ? "shadow-lg" : "shadow-md";

  const menuItems = ["Home", "Destinations", "Packages", "About", "Contact"];

  return (
    <nav
      className={`fixed top-0 left-0 right-0 z-50 bg-white/80 backdrop-blur-xl transition-all duration-300 border-b border-sky-100 ${navHeight} ${shadowClass}`}
    >
      <div className="max-w-7xl mx-auto px-6 flex items-center justify-between">
        {/* Logo */}
        <Link
          href="/"
          className={`text-2xl font-extrabold text-sky-600 tracking-tight transition-transform duration-300 ${logoScale}`}
        >
          Dream<span className="text-gray-800">Travels</span>
        </Link>

        {/* Desktop Menu */}
        <div className="hidden md:flex items-center space-x-10 text-gray-700 font-medium">
          {menuItems.map((item) => (
            <Link
              key={item}
              href={`/${item.toLowerCase()}`}
              className="relative group hover:text-sky-600 transition-all duration-300"
            >
              {item}
              <span className="absolute left-0 -bottom-1 w-0 h-[2px] bg-sky-600 transition-all duration-300 group-hover:w-full"></span>
            </Link>
          ))}

          <Link
            href="/packages"
            className="ml-2 px-5 py-2 bg-sky-600 text-white rounded-xl shadow-md hover:shadow-lg hover:bg-sky-700 transition-all duration-300 transform hover:-translate-y-0.5 hover:scale-105"
          >
            Book Now
          </Link>
        </div>

        {/* Mobile Menu Button */}
        <button
          className="md:hidden text-3xl text-gray-800 hover:text-sky-600 transition-colors"
          onClick={() => setIsOpen(true)}
        >
          <HiOutlineMenuAlt3 />
        </button>
      </div>

      {/* Background Overlay */}
      <div
        className={`fixed inset-0 bg-black/40 backdrop-blur-sm transition-opacity duration-300 ${
          isOpen ? "opacity-100 visible" : "opacity-0 invisible"
        }`}
        onClick={() => setIsOpen(false)}
      ></div>

      {/* Aside Menu */}
      <aside
        className={`fixed top-0 right-0 h-full w-72 bg-white shadow-2xl transform transition-transform duration-500 ease-[cubic-bezier(0.77,0,0.175,1)] ${
          isOpen ? "translate-x-0" : "translate-x-full"
        }`}
      >
        <div className="flex items-center justify-between px-6 py-5 border-b border-gray-200">
          <Link
            href="/"
            onClick={() => setIsOpen(false)}
            className="text-xl font-bold text-sky-600"
          >
            Dream<span className="text-gray-800">Travels</span>
          </Link>
          <button
            onClick={() => setIsOpen(false)}
            className="text-3xl text-gray-700 hover:text-sky-600"
          >
            <HiOutlineX />
          </button>
        </div>

        <div className="flex flex-col mt-6 space-y-2 px-6">
          {menuItems.map((item, idx) => (
            <Link
              key={item}
              href={`/${item.toLowerCase()}`}
              onClick={() => setIsOpen(false)}
              className="block py-3 text-lg text-gray-700 hover:text-sky-600 transition-all duration-300 border-b border-gray-100"
            >
              {item}
            </Link>
          ))}

          <Link
            href="/packages"
            onClick={() => setIsOpen(false)}
            className="mt-4 px-6 py-3 bg-sky-600 text-white rounded-xl text-center shadow-md hover:bg-sky-700 hover:shadow-lg transition-all duration-300"
          >
            Book Now
          </Link>
        </div>
      </aside>
    </nav>
  );
}
